.pendency-main{
    display: flex;
    height: 20vh;
    background-color: rgb(206, 160, 225);  
}
.sales-pendency{
    display: flex;
    width: 90vw;
    margin-left: 2vw;
    margin-right: 2vw;
    padding: 20px 20px;
    position: relative;
    border-radius: 5px;
    background-color: #fff;
    box-shadow:  1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

  .pendency-head3{
      display: flex;
      color: #fff;
      text-align: left;
      font-size: 24px;
      font-weight: bold;
      font-family: 'Times New Roman', Times, serif;
  }
  .pendency-head4{
    display: flex;
    font-size: 20px;
    color: #fff;
    
  }
  .pendency-btn{
      background-color: rgb(206, 160, 225);
      font-size: 20px;
      color: #fff;
      width: 10vw;
      height: 45px;
      
      
  }
  div.scrollmenu {
    background-color: #333;
    overflow: auto;
    white-space: nowrap;
  }
  
  div.scrollmenu a {
    display: inline-block;
    color: white;
    text-align: center;
    padding: 14px;
    text-decoration: none;
  }
  
  div.scrollmenu a:hover {
    background-color: #777;
  }
  table {
    border-collapse: collapse;
    width: 100%;
  }
  
  th, td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }